import { profileAPI } from "../api/api";

const ADD_POST = 'profile/ADD-POST';
const SET_PROFILE = 'profile/SET_PROFILE';
const SET_STATUS = 'profile/SET_STATUS';
const DELETE_POST = 'profile/DELETE_POST';

let initialState = {
   postData: [
      { id: 0, authorID: 228, likes: 0, text: ' ' },
      { id: 1, authorID: 228, likes: 226, text: '      ' },
      { id: 2, authorID: 228, likes: 1653, text: '       ,          ' },
      { id: 3, authorID: 228, likes: 1498, text: '       ' },
   ],
   profile: null,
   status: '',
}

const profile_reducer = (state = initialState, action) => {
   switch (action.type) {
      case ADD_POST: {
         let newPost = {
            id: state.postData.length,
            authorID: 228,
            likes: 0,
            text: action.postBody,
         };
         return {
            ...state,
            postData: [...state.postData, newPost],
            newPostText: '',
         };
      };

      case DELETE_POST: {
         return { ...state, postData: state.postData.filter(p => p.id != action.postId) }
      }

      case SET_PROFILE: {
         return { ...state, profile: action.profile }
      };

      case SET_STATUS: {
         return { ...state, status: action.status }
      };

      default:
         return state;
   }
};

// ACTION CREATORS
export const addPost = (postBody) => ({ type: ADD_POST, postBody });

export const deletePost = (postId) => ({ type: DELETE_POST, postId });

export const setProfile = (profile) => ({ type: SET_PROFILE, profile });

export const setStatus = (status) => ({ type: SET_STATUS, status });

// THUNKS
export const getProfile = (id) => async (dispatch) => {
   let data = await profileAPI.getProfile(id);
   dispatch(setProfile(data));
};

export const getStatus = (id) => async (dispatch) => {
   let data = await profileAPI.getStatus(id);
   dispatch(setStatus(data));
};

export const updateStatus = (status) => async (dispatch) => {
   let data = await profileAPI.updateStatus(status);
   if (data.resultCode === 0) dispatch(setStatus(status));
};

export default profile_reducer;